.cart-container {
  margin: 20px;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 5px;
}

.cart-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.error-message {
  color: red;
}

.empty-cart-message {
  font-style: italic;
}

.cart-item {
  margin-bottom: 20px;
  padding: 10px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.cart-item h3 {
  font-size: 20px;
  margin-bottom: 5px;
}

.cart-item p {
  margin: 5px 0;
}

.remove-button {
  margin-right: 10px;
  background-color: #ff0101;
}
